@import '../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}

.subscribe-section {
  padding: 55px 30px;

  @media screen and(max-width: 767px){
    padding-top: 0;
  }

  .wrapper {
    padding: 0;
    display: flex;

    @media screen and(min-width: 990px){
      padding: 0 55px !important;
    }

  .subscribe-form-hidden {
    display: none;
  }
  
    .phone-container {
      width: 100%;
      position: relative;
      min-height: 200px;

      @media screen and(min-width: 990px){
        width: 50%;
        height: 530px !important;
      }
    
      .phone-inner {
        @media screen and(min-width: 768px){
          margin: 80px 0 0 0;
        }
        
        .phone-image {
          position: relative;
          z-index: -1;
          
          // @media screen and(min-width: 768px){
          //   transform: translateX(-50px) translateY(-120px);
          // }

          @media screen and(min-width: 1250px){
            transform: translateX(calc(10vw)) translateY(-120px);
          }
          
          @media screen and(min-width: 1450px){
            transform: translateX(calc(80px + 10vw)) translateY(-120px);
          }
          
          img {
            max-height: 450px;
            @media screen and(max-width: 767px){
              max-height: 320px;
              // transform: translateX(-50px);
            }
          }
        }
      }
    }
  }
  .promotion-box {
    width: 100%;
    padding: 20px;

    @media screen and(min-width: 900px){
      width: 50%;
      padding: 20px;
    }

    .p-20:nth-child(1) {
      margin: 20px auto 0 auto;

      @media screen and(min-width: 990px){
        width: 100%;
        margin: 20px 0 0 0; 
      }
    }
    .p-20:nth-child(2) {
      margin-top: 55px; 

      @media screen and(min-width: 990px){
        width: 100%;
        margin: 55px auto 0 auto;
      }
    }
    .p-20:nth-child(3) {
      margin-top: 37px; 

      @media screen and(min-width: 990px){
        width: 100%;
        margin: 37px auto 0 auto;
      }
    }

    .input-wrapper-main {
      width: 100%;
      @media screen and(min-width: 1200px){
        width: 80%;
      }

      /*.text {
        font-size: 14px;
        margin: 0 20px !important;
        width: 40px;

        @media screen and(min-width: 1000px){
          font-size: 18px;
          width: 120px;
        }
      }*/
      
      button {
        min-width: 80;
        max-height: 46px;

        @media screen and(min-width: 990px){
          min-width: 130px;
          max-height: 48px;
        }

        p {
          visibility: hidden;

          &:after {
            content: 'Subscribe';
            visibility: visible;
            margin-left: -50px;
            font-size: 14px;
            font-weight: 600;
            color: white;

            @media screen and(min-width: 1000px){
              font-size: 18px;
            }
          }
        }
      }
    }
    
    .title {
      font-size: 30px;
      font-family: $font-after-night;
      color: $color-title;
  
      @media screen and (min-width: 450px) {
        font-size: $font-size-extra-big;
      }
    }
    .title-sec {
      text-align: left;
      @extend %standard-font;
      font-size: $font-size-regular;
      font-weight: $font-weight-heavy;
      color: $color-title;
  
      @media screen and (min-width: 1440px) {
        font-size: $font-size-big;
      }
  
      .title-desc{
        text-align: left;
        width: 60%;
        @extend %standard-font;
        font-size: $font-size-small;
        color: $color-body;
  
        @media screen and (min-width: 1440px) {
          font-size: $font-size-regular;
        }
      }
    }
    .download-now {
      
      a {
        width: 40%;
        float: left;
        margin-right: 15px;
        margin-bottom: 15px;

        @media screen and(min-width: 990px){
          width: 20% !important ;
          margin-right: 20px !important;
          margin-bottom: 0px;
        }
      }

      img {
        max-width: 150px;
        width: 100%;
      }
    }
  }
}
